/* Main container for breadcrumbs */
.breadcrumb {
    margin-left: 80px;
    display: flex;
    align-items: center;
    font-size: 18px;
    padding: 12px 0;
    gap: 10px;
}

/* Breadcrumb separators (arrows) */
.breadcrumb img {
    width: 20px;
    height: 20px;
    margin: 0 10px;
}

/* Breadcrumb items */
.breadcrumb a {
    text-decoration: none;
    color: #ff0004;
    font-weight: 500;
    transition: color 0.3s ease;
}

.breadcrumb a:hover {
    color: #b33900;
    text-decoration: underline;
}

/* Last breadcrumb item */
.breadcrumb span:last-child {
    color: #333;
    margin-right: 0;
}