.navbar{
    display: flex;
    justify-content: space-around;
    padding: 16px;
    box-shadow: 0 1px 3px -2px black;
}
.nav-logo{
    display: flex;
    align-items: center;
    gap: 10px;
}
.nav-logo p{
    color: #171717;
    font-size: 38px;
    font-weight: 600;
}
.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    color: #626262;
    font-size: 20px;
    font-weight: 500;
}
.nav-menu li{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3px;
    cursor: pointer;
}
.nav-menu hr{
    border: none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background: #ff2b2b;
}
.nav-login-cart{
    display: flex;
    align-items: center;
    gap: 45px;
}
.nav-login-cart button{
    width: 157px;
    height: 70px;
    outline: none;
    border: 1px solid #7a7a7a;
    border-radius: 75px;
    color: #515151;
    font-size: 20px;
    font-weight: 500;
    background: white;
    cursor: pointer;
}
.nav-login-cart a{
    text-decoration: none;
}
.nav-login-cart button:active{
    background: #f3f3f3;
}
.nav-cart-count{
    width: 22px;
    height:22px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -35px;
    margin-left: -55px;
    border-radius: 11px;
    font-size: 14px;
    background: red;
    color: white;
}

/* Responsive styles */
@media (max-width: 768px) {
    .navbar {
        flex-direction: column; /* Stack items vertically */
        align-items: center; /* Center align items */
    }

    .nav-menu {
        flex-direction: column; /* Stack menu items vertically */
        gap: 20px; /* Reduce gap for smaller screens */
        font-size: 18px; /* Slightly smaller font size */
    }

    .nav-login-cart {
        flex-direction: column; /* Stack login and cart items */
        gap: 10px; /* Reduce gap */
    }

    .nav-login-cart button {
        width: 100%; /* Make button full-width */
        height: 50px; /* Adjust height for better fit */
        font-size: 18px; /* Slightly smaller font size */
        padding: 10px;
    }

    .nav-cart-count {
        margin-left: 0; /* Adjust position */
        margin-top: -25px; /* Adjust position */
    }
}