/* Main container for the description box */
.descriptionbox {
    border: 1px solid #ddd; /* Border around the box */
    border-radius: 8px; /* Rounded corners */
    padding: 20px; /* Padding inside the box */
    background-color: #fff; /* White background */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
    margin-top: 20px; /* Space above the box */
}

/* Navigation section inside the description box */
.descriptionbox-navigator {
    display: flex; /* Flexbox for alignment */
    border-bottom: 1px solid #ddd; /* Border separating nav and description */
    margin-bottom: 15px; /* Space below the nav section */
}

/* Navigation items */
.descriptionbox-nav-box {
    padding: 10px 20px; /* Padding inside each nav box */
    cursor: pointer; /* Pointer cursor on hover */
    font-weight: 500; /* Slightly bolder font */
    color: #333; /* Dark text color */
    border-radius: 8px; /* Rounded corners for navigation items */
    transition: background-color 0.3s, color 0.3s; /* Smooth transition for hover effects */
}

/* Active navigation item styling */
.descriptionbox-nav-box:not(.fade):hover {
    color: #e74c3c; /* Highlight color on hover */
    background-color: #f2f2f2; /* Light background on hover */
}

/* Styling for the faded nav item */
.descriptionbox-nav-box.fade {
    color: #888; /* Lighter text color */
    border-bottom: 2px solid transparent; /* No underline */
}

/* Description text */
.descriptionbox-description {
    font-size: 16px; /* Font size for description text */
    color: #0c0303; /* Text color */
    border: 1px solid #393636; /* Border around description text */
    border-radius: 8px; /* Rounded corners for description box */
    padding: 15px; /* Padding inside description box */
}

/* Responsive design */
@media (max-width: 768px) {
    .descriptionbox-navigator {
        flex-direction: column; /* Stack navigation items vertically on smaller screens */
    }

    .descriptionbox-nav-box {
        border-bottom: 1px solid #ddd; /* Add border between items in column layout */
    }

    .descriptionbox-nav-box:last-child {
        border-bottom: none; /* Remove border from the last item */
    }
}