.product-display {
    display: flex;
    margin: 20px 0;
    gap: 30px; /* Space between left and right sections */
}

.productdisplay-left {
    display: flex;
    align-items: center;
    gap: 20px; /* Space between small images and main image */
    flex: 1;
}

.productdisplay-img-list {
    margin-left: 30px;
    display: flex;
    flex-direction: column;
    gap: 16px;
    align-items: center; /* Center align thumbnail images */
}

.productdisplay-img-list img {
    height: 120px;
    object-fit: cover;
    cursor: pointer;
    border-radius: 8px; /* Rounded corners for thumbnail images */
}

.product-display-img {
    display: flex;
    justify-content: center;
}

.productdisplay-main-img {
    height: 530px;
    object-fit: cover;
    border: 1px solid #ddd;
    border-radius: 8px; /* Rounded corners for main image */
}

.productdisplay-right {
    flex: 2;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.productdisplay-right-star {
    display: flex;
    align-items: center;
    gap: 5px;
}

.productdisplay-right-star img {
    width: 24px;
    height: 24px;
}

.productdisplay-right-prices {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.productdisplay-right-price-old {
    text-decoration: line-through;
    color: #888;
    font-size: 18px;
}

.productdisplay-right-price-new {
    font-size: 28px;
    font-weight: bold;
    color: #e74c3c;
}

.productdisplay-right-description {
    font-size: 16px;
    color: #333;
}

.productdisplay-right-size {
    display: flex;
    gap: 10px;
    margin-top: 15px;
}

.productdisplay-right-size div {
    display: flex;
    flex-direction: row;
    padding: 12px 20px;
    border: 1px solid #ddd;
    border-radius: 50px; /* Fully rounded corners for size options */
    cursor: pointer;
    text-align: center;
    font-size: 16px;
    gap: 10px;
    transition: background-color 0.3s, border-color 0.3s; /* Smooth transition for hover effects */
}

.productdisplay-right-size div:hover {
    background-color: #f9f9f9;
    border-color: #ccc;
}

button {
    padding: 12px 24px;
    background-color: #ff1900a9;
    color: #fff;
    border: none;
    border-radius: 25px; /* Makes the button corners rounded */
    cursor: pointer;
    font-size: 18px;
    font-weight: bold;
    text-align: center; /* Centers text inside the button */
    display: block; /* Ensures the button takes the full width available */
    margin: 0 auto; /* Centers the button horizontally */
    transition: background-color 0.3s, transform 0.3s; /* Adds transition effects */
}

button:hover {
    background-color: #caf4ff;
    transform: scale(1.05); /* Slightly enlarges the button on hover */
}

.productdisplay-right-category {
    margin-top: 10px;
}

.productdisplay-right-category span {
    font-weight: bold;
    color: #555;
}